@import "../../styles/common";

.scroll {
  width: 4em;
  height: 4em;
  border: 2px solid #333;
  border-radius: 50%;
  position: relative;
  animation: down 1.5s infinite;
  -webkit-animation: down 1.5s infinite;
  margin: 0 auto;
  position: absolute;
  bottom: 2em;
  cursor: pointer;
  left: 0;
  right: 0;
  &::before {
    content: "";
    position: absolute;
    top: 1.2em;
    left: 1.45em;
    width: 1em;
    height: 1em;
    border-left: 2px solid #333;
    border-bottom: 2px solid #333;
    transform: rotate(-45deg);
  }
}
.downloads-section {
  .app-download {
    font-size: 1.8rem;
    text-transform: uppercase;
  }
  .downloads_btns {
    a {
      img {
        height: 2.5rem;
        width: auto;
        max-width: unset;
      }
    }
  }
}
@keyframes down {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translateY(15px);
  }
  40% {
    transform: translate(0);
  }
}

@-webkit-keyframes down {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translateY(15px);
  }
  40% {
    transform: translate(0);
  }
}

