.theme-btn {
  border-radius: 0.3rem;
  text-decoration: none;
  border: solid 1px #000;
  cursor: pointer;
  color: #fff;
  display: block;
  margin: 0 0 1rem;
  padding: 0.6rem 2rem;
  transition: 0.3s;
  position: relative;
  text-transform: uppercase;
  font-size: 1.4rem;
  background: transparent
    linear-gradient(180deg, #e2ff00 0%, #18e0e0 47%, #e8318d 100%) 0% 0%
    no-repeat padding-box;

  &::before {
    content: "";
    position: absolute;
    top: 0.1rem;
    right: 0.1rem;
    bottom: 0.1rem;
    left: 0.1rem;
    background: #000;
    border-radius: 0.3rem;
    z-index: 0;
  }
  &:hover,
  &:focus,
  &:active {
    border: solid 1px #000;
    background: #fff;
    color: #000;
    &::before {
      border: solid 1px #000;
      background: #fff;
      color: #000;
    }
  }
  &:disabled {
    background: #e7e7e9;
    color: #9c9c9b;
    border: none;
    opacity: 1;
    &::before {
      opacity: 0.5;
    }
  }
  span {
    position: relative;
    z-index: 2;
  }
}

.offer {
  cursor: pointer;
  font-size: 1.5rem;
  color: #e1ff04;
  text-decoration: none;
  img {
    height: 1.6rem;
    margin-right: 0.5rem;
  }
}



