/* Container Tooltip */
.tooltip2 {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 40px;
  cursor: pointer;
}

/* Style text tooltip */
.tooltipText2 {
  background-color: var(--base-color);
  // position: absolute;
  bottom: 100%;
  right: -20%;
  position: relative;
  padding: 10px 15px;
  border-radius: 5px;
  font-size: 16px;
  opacity: 0;
  transition: all .5s;
}

.fb {color: #000;}
.tw {color: #1da1f2}
.wa {color:#25d366}


/*  Membuat dan style tip*/
.tooltipText2::after {
  content: '';
  border-width: 5px;
  border-style: solid;
  border-color: var(--base-color) transparent transparent transparent;
  position: absolute;
  top: 100%;
  right: 15%;
  margin-left: 5%;
}



/* Hover text tooltip */
.tooltip2:hover .tooltipText2{
  opacity: 1;
  transform: translateY(-10px);
}

